.buttons-container {
  margin-top: 250px;
  opacity: 0;
  -webkit-animation: fadeIn 0.3s ease-out 25s forwards, 
  enablePointerEvents 0s linear 25s forwards;
          animation: fadeIn 0.3s ease-out 25s forwards, 
  enablePointerEvents 0s linear 25s forwards;
  padding: 0 20px;
  pointer-events: none;
}

.buttons-container.active {
  pointer-events: auto;
}

.btn {
  outline: none;
  border: none;
  font-family: 'Press Start 2P', cursive;
  margin-right: 30px;
  transition: -webkit-transform 0.2s linear;
  transition: transform 0.2s linear;
  transition: transform 0.2s linear, -webkit-transform 0.2s linear;
}

.btn:hover {
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media (min-width: 720px) {
  .buttons-container {
    margin-top: 400px;
  }
}

@-webkit-keyframes enablePointerEvents {
  100% { pointer-events: auto; }
}

@keyframes enablePointerEvents {
  100% { pointer-events: auto; }
}
