.loading {
  color: var(--white);
  font-size: 28px;
  position: relative;
  opacity: 0;
  -webkit-animation: fadeIn 2s ease-out forwards, loadingLeft 0.2s ease-in 0.4s forwards;
          animation: fadeIn 2s ease-out forwards, loadingLeft 0.2s ease-in 0.4s forwards;
  top: inherit;
}

@-webkit-keyframes loadingLeft {
  0% {
    -webkit-transform: translateY(inherit);
            transform: translateY(inherit);
  }

  100% {
    -webkit-transform: translateY(100vh);
            transform: translateY(100vh);
  }
}

@keyframes loadingLeft {
  0% {
    -webkit-transform: translateY(inherit);
            transform: translateY(inherit);
  }

  100% {
    -webkit-transform: translateY(100vh);
            transform: translateY(100vh);
  }
}

@media (min-width: 720px) {
  .loading {
    font-size: 32px;
  }
}
