.avatar-container {
  width: 130px;
  height: 130px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: absolute;
  left: calc(50% - 65px);
  top: -100vh;
  opacity: 0;
  -webkit-animation: avatarEntranceMobile 0.1s ease-out 0.4s forwards;
          animation: avatarEntranceMobile 0.1s ease-out 0.4s forwards;
}

.avatar {
  width: 85px;
  height: 85px;
  overflow: hidden;
  -o-object-fit: contain;
     object-fit: contain;
}

.avatar-bubble {
  min-width: 300px;
  position: absolute;
  top: -100px;
  opacity: 0;
  -webkit-animation: fadeIn 0.3s ease-out 0.8s forwards, floating 0.8s linear alternate infinite;
          animation: fadeIn 0.3s ease-out 0.8s forwards, floating 0.8s linear alternate infinite;
}

@-webkit-keyframes avatarEntrance {
  0% {
  }
  100% {
    opacity: 1;
    top: calc(50vh - 68px);
  }
}

@keyframes avatarEntrance {
  0% {
  }
  100% {
    opacity: 1;
    top: calc(50vh - 68px);
  }
}

@-webkit-keyframes avatarEntranceMobile {
  0% {
  }
  100% {
    opacity: 1;
    top: calc(40vh - 68px);
  }
}

@keyframes avatarEntranceMobile {
  0% {
  }
  100% {
    opacity: 1;
    top: calc(40vh - 68px);
  }
}

@media (min-width: 720px) {
  .avatar-container {
    -webkit-animation: avatarEntrance 0.1s ease-out 0.4s forwards;
            animation: avatarEntrance 0.1s ease-out 0.4s forwards;
  }

  .avatar-bubble {
    min-width: 400px;
  }
}
